import React from "react";

export default (props) => (
  <div>
    <header>Cookies Policy</header>

    <p className="title">What is the purpose of a cookie?</p>
    <p>Cookies are very useful and do lots of different jobs that help to make your experience of a website smoother and more enjoyable. You could say they're like a website’s ‘memories’ of you, helping it recall how you’ve navigated around its pages, and the choices you’ve made along the way. A website that remembers you is much easier and faster to navigate than one that doesn’t. That’s why most of the websites you know use cookies.</p>

    <p className="title">What’s in a cookie?</p>
    <p>A cookie is a simple piece of information in the form of a very small text file. Each and every cookie is unique and comprised of the name of the website that created it and a one-of-a-kind string of digits and letters. The vast majority of websites and apps place cookies on your device’s hard drive (computer, mobile phone or tablet) as you browse them. They do this through your web browser – for example, Google Chrome, Internet Explorer, Safari or Firefox. A cookie is only readable and retrievable by the website that created it; websites are never able to share information through cookies. Some cookies only last for the duration of your visit. These ‘session cookies’ disappear from your computer or mobile device when you close your browser. Other cookies stay on your computer or mobile device after your browser has closed and last for a specified amount of time. These ‘persistent cookies’ are activated each time you visit the site that generated them. What’s not in a cookie? Cookies do NOT contain: Personal information – you cannot be personally identified from a cookie Programme code – unlike a computer virus, a cookie is not comprised of code, which means it is incapable of harming your device</p>

    <p className="title">Are cookies dangerous?</p>
    <p>No Cookies are small and simple strings of characters known as text files. They are not computer programs. They cannot read your personal information. They can’t spread a computer virus. Cookies are unable to harm your device, and they pose no threat to your privacy.</p>

    <p className="title">Does trippur sell cookie data to third parties?</p>
    <p>We will never, sell user data to a third party. This includes information connected to our cookies. If you’d like to know more, please view our Privacy Policy.</p>

    <p className="title">Does trippur use cookies to raise prices in search results?</p>
    <p>No, Trippur never tracks your cookies or IP address to artificially increase prices on flights, hotels or holidays. Since we don’t sell anything ourselves, there will never be a time when we push up prices in your search results.</p>

    <p className="title">How does trippur use cookies?</p>
    <p>When you use one of our websites or apps, the following 4 categories of cookies may be set on your device: Strictly necessary These cookies are essential for you to move around our websites and apps, and use their features. Without these cookies our services won’t work properly. Example Cookies are essential for Trippur to direct you to the suppliers of cheap flights and great hotel deals These cookies do not gather information about you that could be used for marketing or recording where you've been on the internet.</p>

    <p className="title">Functionality</p>
    <p>These cookies allow our websites and apps to remember your choices and preferences. They enable us to provide you with enhanced and personalized features. These cookies do not collect any information that could identify you personally. They do not gather any information about you that could be used to sell advertising or remember where you've been on the internet.</p>

    <p className="title">Performance</p>
    <p>In order to keep Trippur easy to use, relevant and up-to-date, we use web analytics services to help us understand how people use our websites and apps. Web analytics data and cookies cannot be used to identify you as they never contain personal information. They are used solely for our own statistical purposes. We generally use independent measurement and research companies to collect these statistics for us. In such cases, these cookies may be set by a third party company (third party cookies).</p>

    <p className="title">Targeting</p>
    <p>We also use cookies to assist in targeted advertising. These cookies are used to show you ads containing flight and hotel prices we believe you’ll be interested in. You may see these ads while using a website other than Trippur. We tailor our ad campaigns with relevant content because we want to inspire you and enable you in booking your perfect trip. Example: You see an advert when visiting another site elsewhere on the internet showing you flight prices for a destination you previously searched on Trippur Although these cookies can track your visits around the web, they don't know who you are. These cookies do not collect any sensitive personal information about you. We also include links to social networks like Facebook. These sites may subsequently use information about your visit to a Trippur website to target you with ads on their websites.</p>

  </div>
)



