import React from "react";

export default (props) => (
  <div>
    <header>Privacy</header>
    <p>Only the English version of the privacy policy is legally binding. Translations have been provided purely for user convenience. In the event of discrepancy between a translation and the English master, the English master prevails.</p>

    <p>This policy (together with our Terms of Use and any other document referred to in it) sets out the basis on which any personal data we collect from you or you provide to us, will be processed by us.</p>

    <p>This policy applies to the Trippur websites, and mobile applications (the apps) in different markets that are operated by Trippur B.V. (“Trippur”). We take your privacy seriously and provide this privacy policy to inform you of our practices with respect to how we collect, use, and protect personal information about visitors to and members of our website and apps. Personal information is information that is personally identifiable like your name, email address, phone number, mailing address, etc. By using our website and the apps you consent to the terms described in this document. Information about us Trippur B.V. is registered in The Netherlands under company number xxxxxxx. The address of our registered office is Singel 3, Amsterdam, The Netherlands.</p>

    <p className="title">Information collection</p>
    <p>Trippur may collect and process the following data about you:</p>

    <ul>
        <li>Information that you provide by filling in forms on our website or apps. This includes information provided at the time of registering to use our site or apps, subscribing to our service, posting material or requesting further services. We may also ask you for information when you enter a competition or promotion sponsored by us, and when you report a problem with our site or apps.]</li>
        <li>A record of any correspondence with you.</li>
        <li>Details of which services you are interested in.</li>
        <li>Details of your visits to our site and apps including, but not limited to traffic data, location data, web logs and other communication data, whether this is required for our own billing purposes or otherwise, and the resources that you access.</li>
        <li>Information on whether you have opened and clicked on certain content in our emails sent to you.</li>
        <li>Information about your computer, including where available your IP address, operating system, browser type and device ID, for system administration and to report aggregate information to our advertisers; and information obtained from cookies. Please read further under Cookie Policy. This information does not necessarily enable us to personally identify you.</li>
    </ul>

    <p>Trippur does not require that you enter any personal information to search on Trippur’s websites or apps, nor when being transferred to third-party websites. When you register with Trippur, we encourage you to provide information about you such as:</p>

    <ul>
        <li>Your name and email address.</li>
        <li>Information about your flight and hotel room preferences such as seats, departures, arrivals, direct flights, number of passengers, dates of birth and dates of birth of your children for us to store for future use.</li>
        <li>Information about your travel companions. You should obtain the consent of other individuals prior to providing us with their personal information and travel preferences as any access to view or change their information will be available through your profile. If you store information on your children, you are consenting to our processing of such data on behalf of your children.</li>
        <li>Social networking. If you choose to login with Facebook Login <a href="https://developers.facebook.com/products/login/" target="_blank" rel="noopener noreferrer">https://developers.facebook.com/products/login/</a> or Google+ Sign-in <a href="https://developers.google.com/+/features/sign-in" target="_blank" rel="noopener noreferrer">https://developers.google.com/+/features/sign-in</a> you are permitting Trippur to use and store that information related to the profile allowing us to make use of any social media features on our website or apps. The social media provider may send us information in line with their policies which may include your name, profile picture, gender, friend lists and any other information you have chosen to make available. Other information regarding recommendations exchanged between you and your friends may also be shared. Please note that, if your Facebook or Google+ friends are using Trippur, they may have shared information about you with us through Facebook Login or Google+ Sign-in. If you wish to prevent that sharing, you can do so by editing your Facebook or Google+ privacy settings or the relevant settings/preferences for other social media providers.</li>
    </ul>

    <p>It is stated expressly whether the data requested is mandatory or not.</p>

    <p className="title">Information use</p>
    <p>The purpose of the processing of your data are as follows:</p>

    <ul>
        <li>to verify your authorization to access certain features of the site</li>
        <li>to fulfil your requests for products or services</li>
        <li>to personalize and customize the content and relevant trip services that you are offered</li>
        <li>to optimize and personalize your visits by remembering location, language and currency configurations as well as previous searches</li>
        <li>to contact you, e.g. if you have subscribed to a newsletter</li>
        <li>to improve our services</li>
        <li>to provide reporting</li>
    </ul>

    <p className="title">Information sharing and disclosure</p>
    <p>We will never sell your Personal Data to third parties. Trippur shares data about you in the following situations:</p>

    <ul>
        <li>to provide products or services you have requested including, without limitation, products and services from third parties such as:</li>
        <li>suppliers</li>
        <li>third party vendors</li>
        <li>business partners</li>
        <li>referring websites</li>
        <li>where required by law</li>
        <li>or when we have your permission.</li>
    </ul>

    <p>Also, Trippur shares data about you under the following circumstances:</p>

    <ul>
        <li>Trippur may use third party vendors (data processors) to assist us in providing our products or services and may transfer your personally identifiable information to such vendors who have entered into a contract with us that protects the confidentiality of your information and permits the vendor to use it only as necessary to fulfil its contract.</li>
        <li>We may disclose your personal information for any of the purposes mentioned above to any member of our group, which includes our subsidiaries, our ultimate holding company and its subsidiaries. This also includes entities which we may acquire as part of an acquisition at a future date.</li>
        <li>In response to subpoenas, court orders, or legal process, or to establish or exercise our legal rights or defend against legal claims.</li>
        <li>When we believe it is necessary to share information in order to investigate, prevent, or take action regarding illegal activities, suspected fraud, situations involving potential threats to the physical safety of any person, violations of Trippur Terms of use or as otherwise required by law.</li>
    </ul>

    <p className="title">Data storage</p>
    <p>All Personal data you provide to us is stored on our secure servers. At present we have servers located within the EAA (European Economic Area). The data that we collect from you may be transferred to, and stored at, a destination outside the European Economic Area ("EEA"). The transfer may be to another member of the Trippur B.V. companies, subsidiaries or any holdings pertaining or included in the group structure, or to third parties as set out in this policy. It may also be processed by staff operating outside the EEA who work for us or for one of our suppliers. By submitting your personal data, you agree to this transfer, storing or processing. We will take all steps reasonably necessary to ensure that your data is treated securely and in accordance with this privacy policy. We may be required by law, to hold your data in a specific location dependent upon the location from which you will access Trippur services. Regardless of the location in which your data is stored we will take adequate measures to protect the security of your personal data. These countries may have different and/or less stringent privacy/data protection and data security rules than those of your own country. As a result, your personal information may be subject to access requests from governments, courts, or law enforcement in those countries according to laws in those countries.</p>

    <p className="title">Links to third party sites</p>
    <p>Trippur provides links to third party websites, e.g. after a search; and may provide referrals to certain third parties' products or services. If you choose to visit a third party's web site or use its products or services, please be aware that this privacy policy will not govern your activities and any information you disclose to such third party. We strongly encourage you to read the privacy policies of the third party web sites that you visit. We are not in any way responsible for the content of the sites that we provide links to.</p>

    <p className="title">Confidentiality and security</p>
    <p>We limit access to personal information about you to employees who we believe have a legitimate need to come into contact with that information to provide products or services to you or in order to do their jobs. Trippur employs procedural and technical measures reasonably designed to protect your personal information from loss, misuse, unauthorised access or disclosure. While no website or app can guarantee security, we have implemented appropriate administrative, technical, and physical security procedures to help protect your personal information. We also use encryption when transmitting personal information that is sensitive to you between your system and ours.</p>

    <p className="title">Community – Information sharing among Trippur users</p>
    <p>If you choose to include personally identifiable information in your comments, open text fields, or other areas of the web site or the apps intended for public viewing, other users will be able to view and use this information. We recommend against posting your email address and other personally identifiable information in this manner unless you are comfortable with the possibility of receiving unwanted contact. Accessing, reviewing and changing your personal information We will offer you the ability to access, review and change certain information via the registration page on the website or in the app. Upon your request, we will deactivate your profile, and remove your personal information from active service. Your profile will be deactivated as soon as reasonably possible based on your account activity. We will retain in our files and databases some personal information to prevent fraud, resolve disputes, troubleshoot problems, assist with any investigations, enforce our Terms of use and comply with legal requirements as permitted by law. Therefore, you should not expect that all of your personal information will be completely removed from our databases in response to your request.</p>

    <p className="title">Changes to this privacy policy</p>
    <p>Trippur may update this policy. We will notify you about significant changes in the way we treat personal information by sending a notice to the email address specified in your Trippur profile or by placing a notice on our site.</p>

    <p className="title">Owner information</p>
    <p>The Trippur website is provided by Trippur B.V. We are registered in The Netherlands under company number xxxxxxxxxx. The address of our registered office and principal place of business is Singel 3 Amsterdam, The Netherlands.</p>

    <p className="title">Cookies</p>
    <p>The Trippur websites set and access cookies on your computer. A cookie is a small amount of data that Trippur’s servers transfer to your browser and that only Trippur’s servers can read. When you visit the site, the cookie captures specific session data to streamline your experience and, if you select to save your member name and password, the Trippur websites will save this information in a cookie so that you will not be required to sign in each time you visit the site. You may disable or delete cookies from within your Internet browser application by accessing the security or privacy settings. However, Trippur requires you to have cookies enabled to access certain portions of the site. Trippur may allow other companies that show advertisements on some of our pages to set and access their cookies on your computer. Other companies' use of their cookies is subject to their privacy policies, not this one. Other companies do not have access to Trippur’s cookies. The Trippur websites use cookies and other data files (collectively referred to as "Cookies") covered by the EU E-privacy Directive.</p>

    <p><strong>Which information is collected via Cookies?</strong></p>
    <p>Cookies are used by collecting information on the number of users of the website and the traffic on the website. This enables Trippur to adapt content and advertising accordingly and generate statistics of website visits. Also, some of them allow you to share pages with social networks should you wish to do so. They also enable us and our advertisers to serve you with relevant advertisements that may interest you. In addition to Trippur our digital cooperation partners, which are responsible for site support, will have access to data collected via cookies. The information is not disclosed to other third parties. You can always opt out of Cookies, see below. Trippur uses Cookies, including information on IP address, for the following purposes:</p>

    <ul>
        <li>Functionality cookies that will allow our websites and apps to remember your choices and preferences. They enable us to provide you with personalised features.</li>
        <li>Performance cookies used to keep Trippur easy to use, relevant and up-to-date. We use web analytics services to help us understand how people use our websites and apps.</li>
        <li>Targeting cookies used for assisting us in targeted advertising. These cookies are used to show you ads containing flight and hotel prices we believe you’ll be interested in. You may see these ads while using a website other than Trippur. We tailor our ad campaigns with relevant content because we want to inspire and enable you in booking your perfect trip.</li>
    </ul>

    <p><strong>How to delete and manage Cookies?</strong></p>
    <p>As a user, you can decide whether to accept or delete all Cookies, or to control Cookies individually for each website. You can also block the use of Cookies completely. You can make individual settings in your browser settings. Please note that, if you delete Cookies, your access to Trippur’s websites may be more or less limited, or the functionalities at the site may be insufficient.</p>
  </div>
)



